div.row_title {
    float: left;
    width: 70px;
    font-size: 18px;
    font-weight: 600;
    padding: 5px 5px;
    margin: 5px 0;
    border-right: 3px solid black;
}

div.row_input {
    position: relative;
    display: inline-block;
}

div.row_input input[type=text] {
    border: 1px solid #aaa;
    border-radius: 20px;
    padding: 10px 20px;
    margin: 0px 10px;
    font-size: 15px;
    color: rgb(80, 80, 80);
    outline: none;
    transition: 0.3s;
}

div.row_input input[type=text]:focus {
    border-color: rgb(59, 59, 59);
}

div.row_input input[type=text]::placeholder {
    font-size: 15px;
    font-weight: 400;
}